/* #endregion */
/* #region cards slider */
.theTeam_cards-slider-wrapper {
  display: flex;
  position: absolute;
  transition: transform 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
}

.theTeam_cards-slider-wrapper .theTeam_card_Web {
  cursor: pointer;
  /*OTHER WISE IF YOU LESS THE HEIGHT VALUE LIKE (--211) WHITE SPACE WILL SHOW IN THE BOOTOM OF THE DIV*/
  /*min-width: 90%;*/
  /*EVERY ITEMS [theTeam_card] LENGTH WILL NOT SHRNK EVEN FOR SMALLER DEVICES*/
  min-width: 220px;
  /*min-width: 220px;*/
  flex: 1 1;
  opacity: 0.9;
  transform: scale(0.9);
  box-shadow: none;
  transition: opacity 300ms linear, border-color 300ms linear, background-color 300ms linear, transform 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
}

@media only screen and (min-width: 600px) and (max-width: 1318px) {
  .theTeam_cards-slider-wrapper .theTeam_card_Web {
    height: 241px;
  }
}

@media only screen and (min-width: 1318px) {
  .theTeam_cards-slider-wrapper .theTeam_card_Web {
    height: 401px;
  }
}

.theTeam_cards-slider-wrapper .theTeam_card_Web .theTeam_details {
  opacity: 0.9;
  transition: opacity 150ms linear;
}

.theTeam_cards-slider-wrapper-mobile {
  display: flex;
  position: absolute;
  transition: transform 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
  overflow-x: scroll;
  width: 100%;
}

.theTeam_cards-slider-wrapper-mobile .theTeam_cardMobile {
  height: 183px;
  cursor: pointer;
  /*OTHER WISE IF YOU LESS THE HEIGHT VALUE LIKE (--211) WHITE SPACE WILL SHOW IN THE BOOTOM OF THE DIV*/
  /*min-width: 90%;*/
  /*EVERY ITEMS [theTeam_card] LENGTH WILL NOT SHRNK EVEN FOR SMALLER DEVICES*/
  /*min-width: 220px;*/
  flex: 1 1;
  opacity: 0.7;
  transform: scale(0.9);
  box-shadow: none;
  transition: opacity 300ms ease-in-out, border-color 300ms ease-in-out, background-color 300ms ease-in-out, transform 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
}

.theTeam_cards-slider-wrapper-mobile .theTeam_cardMobile .theTeam_details {
  opacity: 0.9;
  transition: opacity 150ms linear;
}

.theTeam_cards-slider-wrapper-mobile::-webkit-scrollbar {
  width: 10px;
  background-color: #FFCB0C;
  /*margin-top:-33px;*/
}

/* Track */
.theTeam_cards-slider-wrapper-mobile::-webkit-scrollbar-track {
  border-radius: 10px;
}

/* Handle */
.theTeam_cards-slider-wrapper-mobile::-webkit-scrollbar-thumb {
  background: #ffffff;
  /*background: darkgrey;*/
  /*background: yellow;*/
  border-radius: 10px;
  /*-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);*/
  /*background-color: #D62929;*/
}

.theTeam_cards-slider-wrapper-mobile::-webkit-scrollbar-thumb:hover {
  /*background: dimgrey;*/
  background: #c9302c;
}

